___CSS_LOADER_EXPORT___.push([module.id, "/* \n * SheetCraft AI — Redesigned Interface\n * A warm, human-crafted design system.\n * Forest green + terracotta. No AI-purple gradients.\n */\n\n:root {\n    /* ── Surface & Layout ── */\n    --bg: #F5F4F1;\n    --surface: #FAFAF8;\n    --card: #FFFFFF;\n    --card-border: #E3E1DC;\n    --card-hover: #F0EFEB;\n    --input-bg: #F5F4F1;\n\n    /* ── Primary: Forest Green ── */\n    --primary: #2D6A4F;\n    --primary-hover: #1B4D3E;\n    --primary-light: #74C69D;\n    --primary-bg: rgba(45, 106, 79, 0.06);\n    --primary-glow: rgba(45, 106, 79, 0.12);\n\n    /* ── Accent: Terracotta ── */\n    --accent: #C4603D;\n    --accent-hover: #A8502F;\n    --accent-bg: rgba(196, 96, 61, 0.07);\n\n    /* ── Text ── */\n    --text: #1A1C1E;\n    --text-2: #555759;\n    --text-3: #8C8E91;\n    --text-4: #C5C7CA;\n\n    /* ── Semantic ── */\n    --success: #2D6A4F;\n    --success-bg: rgba(45, 106, 79, 0.07);\n    --error: #B83A3A;\n    --error-bg: rgba(184, 58, 58, 0.07);\n    --warning: #C08B2D;\n    --warning-bg: rgba(192, 139, 45, 0.07);\n\n    /* ── Shape ── */\n    --radius: 10px;\n    --radius-lg: 14px;\n    --radius-xl: 20px;\n\n    /* ── Depth — warm tinted shadows ── */\n    --shadow-sm: 0 1px 2px rgba(30, 25, 20, 0.04);\n    --shadow: 0 2px 5px rgba(30, 25, 20, 0.05), 0 1px 2px rgba(30, 25, 20, 0.03);\n    --shadow-md: 0 4px 12px rgba(30, 25, 20, 0.06), 0 1px 3px rgba(30, 25, 20, 0.04);\n    --shadow-lg: 0 8px 24px rgba(30, 25, 20, 0.08), 0 2px 6px rgba(30, 25, 20, 0.04);\n\n    /* ── Typography ── */\n    --font: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;\n    --mono: 'JetBrains Mono', 'Cascadia Code', 'Menlo', monospace;\n\n    /* ── Chat ── */\n    --chat-user-bg: var(--primary);\n    --chat-user-text: #FFFFFF;\n    --chat-ai-bg: #F0EFEB;\n    --chat-ai-border: #E3E1DC;\n    --chat-ai-text: var(--text);\n}\n\n/* ── Dark mode ── */\n@media (prefers-color-scheme: dark) {\n    :root {\n        --bg: #141516;\n        --surface: #1A1B1D;\n        --card: #212224;\n        --card-border: #313335;\n        --card-hover: #2A2B2D;\n        --input-bg: #19191B;\n\n        --primary: #52B788;\n        --primary-hover: #74C69D;\n        --primary-light: #2D6A4F;\n        --primary-bg: rgba(82, 183, 136, 0.08);\n        --primary-glow: rgba(82, 183, 136, 0.14);\n\n        --accent: #E0915A;\n        --accent-hover: #EAA876;\n        --accent-bg: rgba(224, 145, 90, 0.09);\n\n        --text: #E5E3DE;\n        --text-2: #9B9D9F;\n        --text-3: #606264;\n        --text-4: #3A3C3E;\n\n        --success: #52B788;\n        --success-bg: rgba(82, 183, 136, 0.09);\n        --error: #E06060;\n        --error-bg: rgba(224, 96, 96, 0.09);\n        --warning: #E0B85A;\n        --warning-bg: rgba(224, 184, 90, 0.09);\n\n        --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.18);\n        --shadow: 0 2px 5px rgba(0, 0, 0, 0.22);\n        --shadow-md: 0 4px 12px rgba(0, 0, 0, 0.28);\n        --shadow-lg: 0 8px 24px rgba(0, 0, 0, 0.32);\n\n        --chat-user-bg: var(--primary);\n        --chat-user-text: #141516;\n        --chat-ai-bg: #262729;\n        --chat-ai-border: #313335;\n        --chat-ai-text: var(--text);\n    }\n}\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* RESET & BASE                                                    */\n/* ═══════════════════════════════════════════════════════════════ */\n*, *::before, *::after { box-sizing: border-box; }\n\nhtml, body {\n    width: 100%; height: 100%;\n    margin: 0; padding: 0;\n    font-family: var(--font);\n    font-size: 13px;\n    line-height: 1.55;\n    background: var(--bg);\n    color: var(--text);\n    -webkit-font-smoothing: antialiased;\n    -moz-osx-font-smoothing: grayscale;\n    overflow-x: hidden;\n}\n\n#app-body {\n    display: flex;\n    flex-direction: column;\n    height: 100vh;\n    overflow: hidden;\n}\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* HEADER                                                          */\n/* ═══════════════════════════════════════════════════════════════ */\n.app-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 11px 16px;\n    background: var(--surface);\n    border-bottom: 1px solid var(--card-border);\n    position: sticky;\n    top: 0;\n    z-index: 20;\n}\n\n.brand {\n    display: flex;\n    align-items: center;\n    gap: 9px;\n}\n\n.logo {\n    color: var(--primary);\n    display: flex;\n    align-items: center;\n}\n\n.brand h1 {\n    font-size: 15px;\n    font-weight: 700;\n    margin: 0;\n    letter-spacing: -0.025em;\n    color: var(--text);\n}\n\n.highlight-text {\n    color: var(--primary);\n    font-weight: 700;\n    /* Solid color, not a gradient — feels hand-picked */\n}\n\n.header-actions {\n    display: flex;\n    gap: 5px;\n}\n\n\n/* ── Icon Buttons ── */\n.btn-icon {\n    width: 32px; height: 32px;\n    border: 1px solid var(--card-border);\n    border-radius: 9px;\n    background: var(--card);\n    color: var(--text-3);\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: color 0.2s ease-out, border-color 0.2s ease-out, background 0.2s ease-out;\n    padding: 0;\n}\n\n.btn-icon:hover {\n    color: var(--primary);\n    border-color: var(--primary);\n    background: var(--primary-bg);\n}\n\n.btn-icon:active {\n    transform: scale(0.95);\n}\n\n\n/* ─── Header ─── */\n.app-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 11px 16px;\n    background: var(--surface);\n    border-bottom: 1px solid var(--card-border);\n    position: sticky;\n    top: 0;\n    z-index: 20;\n}\n\n/* ... (skip unchanged) ... */\n\n/* ─── Panels (Docs / Settings) ─── */\n.panel {\n    padding: 16px;\n    background: var(--card);\n    border-bottom: 1px solid var(--card-border);\n    animation: slideDown 0.22s ease-out;\n}\n\n.panel h3 {\n    font-size: 11px;\n    font-weight: 700; /* Fixed from 650 */\n    text-transform: uppercase;\n    letter-spacing: 0.06em;\n    color: var(--text-3);\n    margin: 0 0 12px;\n}\n\n\n/* ── Docs Grid ── */\n.docs-grid {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.doc-item {\n    display: flex;\n    gap: 10px;\n    align-items: flex-start;\n    padding: 5px 0;\n}\n\n.doc-icon {\n    flex-shrink: 0;\n    width: 18px; height: 18px;\n    color: var(--primary);\n    display: flex;\n    align-items: center;\n    margin-top: 1px;\n}\n\n.doc-item strong {\n    font-size: 12px;\n    font-weight: 600;\n    color: var(--text);\n    display: block;\n    margin-bottom: 2px;\n}\n\n.doc-item p {\n    font-size: 11px;\n    color: var(--text-2);\n    margin: 0;\n    line-height: 1.45;\n}\n\n.docs-hint {\n    font-size: 11px;\n    color: var(--text-3);\n    margin: 12px 0 0;\n    font-style: italic;\n    line-height: 1.5;\n}\n\n\n/* ── Forms ── */\n.form-group { margin-bottom: 12px; }\n\n.form-group label {\n    display: block;\n    font-size: 11px;\n    font-weight: 600;\n    color: var(--text-3);\n    margin-bottom: 4px;\n    text-transform: uppercase;\n    letter-spacing: 0.04em;\n}\n\n.form-input {\n    width: 100%;\n    padding: 8px 11px;\n    font-size: 13px;\n    font-family: var(--font);\n    color: var(--text);\n    background: var(--input-bg);\n    border: 1px solid var(--card-border);\n    border-radius: 9px;\n    outline: none;\n    transition: border-color 0.2s ease-out, box-shadow 0.2s ease-out;\n}\n\n.form-input:focus {\n    border-color: var(--primary);\n    box-shadow: 0 0 0 3px var(--primary-glow);\n}\n\nselect.form-input {\n    cursor: pointer;\n    appearance: none;\n    background-image: url(" + ___CSS_LOADER_URL_REPLACEMENT_0___ + ");\n    background-repeat: no-repeat;\n    background-position: right 10px center;\n    padding-right: 28px;\n}\n\n.btn-save {\n    width: 100%;\n    justify-content: center;\n    margin-top: 6px;\n}\n\n/* Model selector row */\n.model-select-wrapper {\n    display: flex;\n    gap: 5px;\n    align-items: center;\n}\n\n.model-select-wrapper .form-input {\n    flex: 1;\n    min-width: 0;\n}\n\n.btn-refresh {\n    width: 30px; height: 30px;\n    flex-shrink: 0;\n    padding: 0;\n}\n\n.model-status {\n    font-size: 11px;\n    margin-top: 4px;\n    min-height: 15px;\n    color: var(--text-3);\n}\n\n.model-status-ok  { color: var(--success); }\n.model-status-warn { color: var(--error); }\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* MODE TOGGLE                                                     */\n/* ═══════════════════════════════════════════════════════════════ */\n.mode-toggle {\n    display: flex;\n    align-items: center;\n    padding: 6px;\n    margin: 0;\n    background: var(--surface);\n    border-bottom: 1px solid var(--card-border);\n    position: relative;\n    gap: 4px;\n}\n\n.mode-tab {\n    flex: 1;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 6px;\n    padding: 9px 14px;\n    font-size: 12.5px;\n    font-weight: 600;\n    font-family: var(--font);\n    color: var(--text-3);\n    background: transparent;\n    border: none;\n    border-radius: 9px;\n    cursor: pointer;\n    transition: color 0.25s ease-out;\n    position: relative;\n    z-index: 2;\n}\n\n.mode-tab:hover { color: var(--text-2); }\n.mode-tab.active { color: var(--primary); }\n.mode-tab svg { width: 14px; height: 14px; }\n\n.mode-indicator {\n    position: absolute;\n    left: 6px;\n    top: 6px;\n    width: calc(50% - 8px);\n    height: calc(100% - 12px);\n    background: var(--primary-bg);\n    border: 1px solid var(--primary-glow);\n    border-radius: 9px;\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n    z-index: 1;\n}\n\n.mode-indicator.right {\n    transform: translateX(calc(100% + 4px));\n}\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* MODE CONTENT WRAPPER                                            */\n/* ═══════════════════════════════════════════════════════════════ */\n.mode-content {\n    display: none;\n    flex-direction: column;\n    flex: 1;\n    overflow: hidden;\n    animation: fadeIn 0.2s ease-out;\n}\n\n.mode-content.active { display: flex; }\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* PLANNING MODE — Chat                                            */\n/* ═══════════════════════════════════════════════════════════════ */\n\n/* ── Chat Messages Container ── */\n.chat-messages {\n    flex: 1;\n    overflow-y: auto;\n    padding: 14px 14px 6px;\n    display: flex;\n    flex-direction: column;\n    gap: 14px;\n    scroll-behavior: smooth;\n}\n\n\n/* ── Welcome Screen ── */\n.chat-welcome {\n    text-align: center;\n    padding: 28px 18px 18px;\n    animation: fadeIn 0.35s ease-out;\n}\n\n.welcome-icon {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    width: 52px; height: 52px;\n    background: var(--primary);\n    border-radius: 16px;\n    color: white;\n    margin-bottom: 14px;\n    box-shadow: 0 4px 14px rgba(45, 106, 79, 0.2);\n}\n\n.welcome-icon svg { width: 24px; height: 24px; }\n\n.chat-welcome h2 {\n    font-size: 17px;\n    font-weight: 700;\n    margin: 0 0 8px;\n    color: var(--text);\n    letter-spacing: -0.02em;\n}\n\n.chat-welcome p {\n    font-size: 12.5px;\n    color: var(--text-2);\n    margin: 0 0 18px;\n    line-height: 1.65;\n    max-width: 270px;\n    margin-left: auto;\n    margin-right: auto;\n}\n\n\n/* ── Chat Suggestions ── */\n.welcome-suggestions {\n    display: flex;\n    flex-direction: column;\n    gap: 7px;\n    max-width: 280px;\n    margin: 0 auto;\n}\n\n.suggestion-chip {\n    display: flex;\n    align-items: center;\n    gap: 9px;\n    padding: 10px 13px;\n    background: var(--card);\n    border: 1px solid var(--card-border);\n    border-radius: var(--radius);\n    font-size: 11.5px;\n    color: var(--text-2);\n    cursor: pointer;\n    transition: all 0.22s ease-out;\n    text-align: left;\n    font-family: var(--font);\n    line-height: 1.4;\n}\n\n.suggestion-chip:hover {\n    border-color: var(--primary);\n    color: var(--primary);\n    background: var(--primary-bg);\n    transform: translateX(3px);\n}\n\n.suggestion-chip svg {\n    flex-shrink: 0;\n    color: var(--primary);\n    opacity: 0.5;\n}\n\n.suggestion-chip:hover svg { opacity: 1; }\n\n\n/* ── Chat Message Bubbles ── */\n.chat-msg {\n    display: flex;\n    gap: 9px;\n    animation: msgSlideIn 0.25s ease-out;\n    max-width: 100%;\n}\n\n.chat-msg.user {\n    flex-direction: row-reverse;\n}\n\n.chat-avatar {\n    width: 28px; height: 28px;\n    border-radius: 9px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n    font-size: 11px;\n    font-weight: 600;\n}\n\n.chat-msg.user .chat-avatar {\n    background: var(--primary);\n    color: white;\n}\n\n.chat-msg.ai .chat-avatar {\n    background: var(--primary-bg);\n    color: var(--primary);\n    border: 1px solid var(--primary-glow);\n}\n\n.chat-bubble {\n    padding: 11px 14px;\n    border-radius: var(--radius-lg);\n    font-size: 12.5px;\n    line-height: 1.65;\n    max-width: 82%;\n    word-wrap: break-word;\n    overflow-wrap: break-word;\n}\n\n.chat-msg.user .chat-bubble {\n    background: var(--chat-user-bg);\n    color: var(--chat-user-text);\n    border-bottom-right-radius: 5px;\n}\n\n.chat-msg.ai .chat-bubble {\n    background: var(--chat-ai-bg);\n    color: var(--chat-ai-text);\n    border: 1px solid var(--chat-ai-border);\n    border-bottom-left-radius: 5px;\n}\n\n\n/* ── Chat formatted text ── */\n.chat-bubble strong { font-weight: 600; }\n.chat-bubble em { font-style: italic; opacity: 0.9; }\n\n.chat-bubble code {\n    font-family: var(--mono);\n    font-size: 11px;\n    padding: 2px 5px;\n    border-radius: 5px;\n    background: rgba(0, 0, 0, 0.06);\n}\n\n.chat-msg.user .chat-bubble code {\n    background: rgba(255, 255, 255, 0.18);\n}\n\n.chat-bubble ul, .chat-bubble ol { margin: 6px 0; padding-left: 18px; }\n.chat-bubble li { margin-bottom: 3px; }\n.chat-bubble p { margin: 0 0 8px; }\n.chat-bubble p:last-child { margin: 0; }\n\n\n/* ── Execute from Chat ── */\n.chat-action-bar {\n    display: flex;\n    gap: 6px;\n    margin-top: 10px;\n}\n\n.btn-execute-from-chat {\n    display: inline-flex;\n    align-items: center;\n    gap: 5px;\n    padding: 5px 11px;\n    font-size: 10.5px;\n    font-weight: 600;\n    font-family: var(--font);\n    background: var(--primary-bg);\n    color: var(--primary);\n    border: 1px solid var(--primary-glow);\n    border-radius: 7px;\n    cursor: pointer;\n    transition: all 0.2s ease-out;\n}\n\n.btn-execute-from-chat:hover {\n    background: var(--primary);\n    color: white;\n    box-shadow: 0 2px 8px rgba(45, 106, 79, 0.25);\n}\n\n\n/* ── Typing Indicator ── */\n.typing-indicator {\n    display: flex;\n    gap: 4px;\n    align-items: center;\n    padding: 4px 0;\n}\n\n.typing-dot {\n    width: 6px; height: 6px;\n    background: var(--text-3);\n    border-radius: 50%;\n    animation: typingBounce 0.6s ease-in-out infinite;\n}\n\n.typing-dot:nth-child(2) { animation-delay: 0.15s; }\n.typing-dot:nth-child(3) { animation-delay: 0.3s; }\n\n\n/* ── Chat Input Area ── */\n.chat-input-area {\n    padding: 10px 14px 8px;\n    background: var(--surface);\n    border-top: 1px solid var(--card-border);\n}\n\n.chat-input-card {\n    display: flex;\n    align-items: flex-end;\n    gap: 7px;\n    background: var(--card);\n    border: 1px solid var(--card-border);\n    border-radius: var(--radius-lg);\n    padding: 5px 5px 5px 14px;\n    transition: border-color 0.2s ease-out, box-shadow 0.2s ease-out;\n    box-shadow: var(--shadow-sm);\n}\n\n.chat-input-card:focus-within {\n    border-color: var(--primary);\n    box-shadow: 0 0 0 3px var(--primary-glow);\n}\n\n#chat-input {\n    flex: 1;\n    border: none;\n    background: transparent;\n    font-family: var(--font);\n    font-size: 13px;\n    color: var(--text);\n    padding: 8px 0;\n    resize: none;\n    outline: none;\n    min-height: 20px;\n    max-height: 84px;\n    line-height: 1.5;\n}\n\n#chat-input::placeholder { color: var(--text-3); }\n\n.btn-send {\n    width: 34px; height: 34px;\n    border: none;\n    border-radius: 9px;\n    background: var(--primary);\n    color: white;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n    transition: background 0.2s ease-out, transform 0.15s ease-out;\n    box-shadow: 0 2px 6px rgba(45, 106, 79, 0.2);\n}\n\n.btn-send:hover {\n    background: var(--primary-hover);\n    transform: scale(1.04);\n}\n\n.btn-send:active { transform: scale(0.94); }\n\n.btn-send:disabled {\n    opacity: 0.35;\n    cursor: not-allowed;\n    transform: none;\n}\n\n.btn-send svg { width: 14px; height: 14px; }\n\n.chat-footer {\n    display: flex;\n    justify-content: center;\n    padding: 4px 0 0;\n}\n\n.btn-text {\n    display: inline-flex;\n    align-items: center;\n    gap: 4px;\n    padding: 5px 9px;\n    font-size: 11px;\n    font-weight: 500;\n    font-family: var(--font);\n    color: var(--text-3);\n    background: transparent;\n    border: none;\n    cursor: pointer;\n    border-radius: 7px;\n    transition: color 0.15s, background 0.15s;\n}\n\n.btn-text:hover {\n    color: var(--error);\n    background: var(--error-bg);\n}\n\n.btn-text svg { width: 12px; height: 12px; }\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* AGENT MODE                                                      */\n/* ═══════════════════════════════════════════════════════════════ */\n.content-wrapper {\n    flex: 1;\n    padding: 14px;\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n    width: 100%;\n    overflow-y: auto;\n}\n\n\n/* ── Category Tabs ── */\n.action-categories {\n    display: flex;\n    gap: 5px;\n    overflow-x: auto;\n    padding: 2px 0;\n    scrollbar-width: none;\n}\n\n.action-categories::-webkit-scrollbar { display: none; }\n\n.category-tab {\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    padding: 7px 11px;\n    font-size: 11px;\n    font-weight: 600;\n    font-family: var(--font);\n    color: var(--text-3);\n    background: transparent;\n    border: 1px solid transparent;\n    border-radius: 9px;\n    cursor: pointer;\n    transition: all 0.2s ease-out;\n    white-space: nowrap;\n    flex-shrink: 0;\n}\n\n.category-tab:hover {\n    color: var(--text-2);\n    background: var(--card);\n}\n\n.category-tab.active {\n    color: var(--primary);\n    background: var(--primary-bg);\n    border-color: var(--primary-glow);\n}\n\n.category-tab svg { width: 12px; height: 12px; }\n\n\n/* ── Quick Action Chips ── */\n.quick-actions {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 6px;\n}\n\n.chip {\n    display: inline-flex;\n    align-items: center;\n    gap: 5px;\n    padding: 6px 11px;\n    font-size: 11px;\n    font-weight: 500;\n    font-family: var(--font);\n    color: var(--text-2);\n    background: var(--card);\n    border: 1px solid var(--card-border);\n    border-radius: 20px;\n    cursor: pointer;\n    transition: all 0.2s ease-out;\n    white-space: nowrap;\n}\n\n.chip:hover {\n    color: var(--primary);\n    border-color: var(--primary);\n    background: var(--primary-bg);\n    transform: translateY(-1px);\n    box-shadow: var(--shadow-sm);\n}\n\n.chip:active {\n    transform: scale(0.96) translateY(0);\n}\n\n.chip svg { width: 12px; height: 12px; flex-shrink: 0; }\n\n\n/* ── Input Card (Agent) ── */\n.input-card {\n    background: var(--card);\n    border: 1px solid var(--card-border);\n    border-radius: var(--radius-lg);\n    display: flex;\n    flex-direction: column;\n    box-shadow: var(--shadow);\n    overflow: hidden;\n    transition: border-color 0.2s ease-out, box-shadow 0.2s ease-out;\n}\n\n.input-card:focus-within {\n    border-color: var(--primary);\n    box-shadow: 0 0 0 3px var(--primary-glow);\n}\n\ntextarea {\n    width: 100%;\n    min-height: 84px;\n    padding: 12px 14px;\n    background: transparent;\n    border: none;\n    color: var(--text);\n    font-family: var(--font);\n    font-size: 13px;\n    line-height: 1.6;\n    resize: vertical;\n    outline: none;\n}\n\ntextarea::placeholder {\n    color: var(--text-3);\n    opacity: 0.85;\n}\n\n.card-footer {\n    padding: 8px 12px;\n    border-top: 1px solid var(--card-border);\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 8px;\n}\n\n\n/* ── Cache Badge ── */\n.cache-badge {\n    font-size: 10.5px;\n    font-weight: 600;\n    color: var(--success);\n    background: var(--success-bg);\n    padding: 3px 9px;\n    border-radius: 10px;\n    animation: fadeIn 0.2s;\n}\n\n\n/* ── Primary Button ── */\n.btn-primary {\n    background: var(--primary);\n    color: white;\n    border: none;\n    padding: 8px 16px;\n    border-radius: 9px;\n    font-size: 12px;\n    font-weight: 600;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    gap: 6px;\n    transition: background 0.2s ease-out, transform 0.15s ease-out, box-shadow 0.2s ease-out;\n    box-shadow: 0 2px 6px rgba(45, 106, 79, 0.2);\n}\n\n.btn-primary:hover {\n    background: var(--primary-hover);\n    transform: translateY(-1px);\n    box-shadow: 0 4px 14px rgba(45, 106, 79, 0.25);\n}\n\n.btn-primary:active {\n    transform: translateY(0);\n}\n\n.btn-primary:disabled {\n    opacity: 0.4;\n    cursor: not-allowed;\n    transform: none;\n}\n\n.btn-primary svg { width: 13px; height: 13px; }\n\n\n/* ── Skeleton Loading ── */\n.skeleton-container {\n    display: flex;\n    flex-direction: column;\n    gap: 9px;\n    padding: 4px 0;\n}\n\n.skeleton-pill {\n    width: 140px;\n    height: 28px;\n    border-radius: 9px;\n    background: linear-gradient(90deg, var(--card-border) 25%, var(--input-bg) 50%, var(--card-border) 75%);\n    background-size: 200% 100%;\n    animation: shimmer 1.6s ease-in-out infinite;\n}\n\n.skeleton-line {\n    height: 13px;\n    border-radius: 5px;\n    background: linear-gradient(90deg, var(--card-border) 25%, var(--input-bg) 50%, var(--card-border) 75%);\n    background-size: 200% 100%;\n    animation: shimmer 1.6s ease-in-out infinite;\n}\n\n.skeleton-line.w80 { width: 80%; }\n.skeleton-line.w60 { width: 60%; }\n\n\n/* ── Status Pill ── */\n.status-pill {\n    padding: 9px 13px;\n    border-radius: var(--radius);\n    font-size: 12px;\n    font-weight: 500;\n    display: none;\n    align-items: center;\n    gap: 7px;\n    animation: fadeIn 0.2s;\n    border: 1px solid transparent;\n}\n\n.status-pill.info {\n    background: var(--primary-bg);\n    color: var(--primary);\n    border-color: var(--primary-glow);\n}\n\n.status-pill.success {\n    background: var(--success-bg);\n    color: var(--success);\n    border-color: rgba(45, 106, 79, 0.12);\n}\n\n.status-pill.error {\n    background: var(--error-bg);\n    color: var(--error);\n    border-color: rgba(184, 58, 58, 0.12);\n}\n\n\n/* ── Spinners ── */\n.spinner {\n    width: 14px; height: 14px;\n    border: 2px solid transparent;\n    border-radius: 50%;\n    border-top-color: currentColor;\n    border-right-color: currentColor;\n    animation: spin 0.7s linear infinite;\n}\n\n.btn-spinner {\n    width: 13px; height: 13px;\n    border: 2px solid rgba(255, 255, 255, 0.3);\n    border-radius: 50%;\n    border-top-color: white;\n    animation: spin 0.7s linear infinite;\n    display: inline-block;\n}\n\n\n/* ── Debug Section ── */\n#debug-section {\n    margin-top: auto;\n    padding-top: 8px;\n}\n\ndetails {\n    background: transparent;\n    border: none;\n    border-radius: var(--radius);\n}\n\nsummary {\n    padding: 5px 0;\n    cursor: pointer;\n    font-size: 11px;\n    font-weight: 600;\n    color: var(--text-3);\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    list-style: none;\n    user-select: none;\n    transition: color 0.15s;\n}\n\nsummary::-webkit-details-marker { display: none; }\nsummary:hover { color: var(--text-2); }\ndetails[open] summary svg { transform: rotate(180deg); }\nsummary svg { transition: transform 0.15s; }\ndetails[open] summary { margin-bottom: 5px; }\n\npre {\n    margin: 0;\n    padding: 11px;\n    background: var(--input-bg);\n    color: var(--text-2);\n    font-family: var(--mono);\n    font-size: 11px;\n    overflow-x: auto;\n    border: 1px solid var(--card-border);\n    border-radius: 9px;\n    line-height: 1.55;\n    white-space: pre-wrap;\n    word-break: break-all;\n    max-height: 180px;\n}\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* SIDELOAD — Skeleton Loading Screen                              */\n/* ═══════════════════════════════════════════════════════════════ */\n.sideload-container {\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n    background: var(--bg);\n    overflow: hidden;\n}\n\n.sideload-skeleton {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    height: 100%;\n    animation: fadeIn 0.3s ease-out;\n}\n\n.sk-shimmer {\n    background: linear-gradient(90deg, var(--card-border) 25%, var(--input-bg) 50%, var(--card-border) 75%);\n    background-size: 200% 100%;\n    animation: shimmer 1.8s ease-in-out infinite;\n    border-radius: 7px;\n}\n\n.sk-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 13px 16px;\n    border-bottom: 1px solid var(--card-border);\n}\n\n.sk-brand { display: flex; align-items: center; gap: 9px; }\n.sk-logo { width: 24px; height: 24px; border-radius: 7px; }\n.sk-title { width: 105px; height: 15px; border-radius: 5px; }\n.sk-header-actions { display: flex; gap: 5px; }\n.sk-icon-btn { width: 32px; height: 32px; border-radius: 9px; }\n\n.sk-mode-toggle {\n    display: flex;\n    gap: 4px;\n    padding: 8px 6px;\n    border-bottom: 1px solid var(--card-border);\n}\n\n.sk-mode-tab { flex: 1; height: 36px; border-radius: 9px; }\n\n.sk-welcome {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    padding: 34px 20px 18px;\n    gap: 11px;\n}\n\n.sk-welcome-icon { width: 52px; height: 52px; border-radius: 16px; }\n.sk-welcome-title { width: 145px; height: 19px; border-radius: 6px; }\n.sk-welcome-desc { width: 220px; height: 13px; border-radius: 5px; }\n.sk-welcome-desc.short { width: 175px; }\n\n.sk-suggestions {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n    padding: 10px 32px;\n}\n\n.sk-suggestion { width: 100%; height: 38px; border-radius: 10px; }\n.sk-suggestion:nth-child(2) { animation-delay: 0.1s; }\n.sk-suggestion:nth-child(3) { animation-delay: 0.2s; }\n.sk-suggestion:nth-child(4) { animation-delay: 0.3s; }\n\n.sk-input-area {\n    margin-top: auto;\n    padding: 12px 16px 8px;\n    border-top: 1px solid var(--card-border);\n}\n\n.sk-input { width: 100%; height: 42px; border-radius: 14px; }\n\n.sideload-status {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 9px;\n    padding: 11px;\n    font-size: 11.5px;\n    font-weight: 500;\n    color: var(--text-3);\n}\n\n.sideload-pulse {\n    width: 8px; height: 8px;\n    border-radius: 50%;\n    background: var(--primary);\n    animation: pulse 1.5s ease-in-out infinite;\n    box-shadow: 0 0 8px var(--primary-glow);\n}\n\n\n/* ═══════════════════════════════════════════════════════════════ */\n/* KEYFRAMES                                                       */\n/* ═══════════════════════════════════════════════════════════════ */\n@keyframes fadeIn {\n    from { opacity: 0; transform: translateY(3px); }\n    to   { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes slideDown {\n    from { opacity: 0; transform: translateY(-6px); }\n    to   { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes spin {\n    to { transform: rotate(360deg); }\n}\n\n@keyframes shimmer {\