0%; }\n.skeleton-line.w60 { width: 60%; }\n\n\n/* ── Status Pill ── */\n.status-pill {\n    padding: 9px 13px;\n    border-radius: var(--radius);\n    font-size: 12px;\n    font-weight: 500;\n    display: none;\n    align-items: center;\n    gap: 7px;\n    animation: fadeIn 0.2s;\n    border: 1px solid transparent;\n}\n\n.status-pill.info {\n    background: var(--primary-bg);\n    color: var(--primary);\n    border-color: var(--primary-glow);\n}\n\n.status-pill.success {\n    background: var(--success-bg);\n    color: var(--success);\n    border-color: rgba(45, 106, 79, 0.12);\n}\n\n.status-pill.error {\n    background: var(--error-bg);\n    color: var(--error);\n    border-color: rgba(184, 58, 58, 0.12);\n}\n\n\n/* ── Spinners ── */\n.spinner {\n    width: 14px; height: 14px;\n    border: 2px solid transparent;\n    border-radius: 50%;\n    border-top-color: currentColor;\n    border-right-color: currentColor;\n    animation: spin 0.7s linear infinite;\n}\n\n.btn-spinner {\n    width: 13px; height: 13px;\n    border: 2px solid rgba(255, 255, 255, 0.3);\n    border-radius: 50%;\n    border-top-color: white;\n    animation: spin 0.7s linear infinite;\n    display: inline-block;\n}\n\n\n/* ── Debug Section ── */\n#debug-section {\n    margin-top: auto;\n    padding-top: 8px;\n}\n\ndetails {\n    background: transparent;\n    border: none;\n    border-radius: var(--radius);\n}\n\nsummary {\n    padding: 5px 0;\n    cursor: pointer;\n    font-size: 11px;\n    font-weight: 600;\n    color: var(--text-3);\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    list-style: none;\n    user-select: none;\n    transition: color 0.15s;\n}\n\nsummary::-webkit-details-marker { display: none; }\nsummary:hover { color: var(--text-2); }\ndetails[open] summary svg { transform: rotate(180deg); }\nsummary svg { transition: transform 0.15s; }\ndetails[open] summary { margin-bottom: 5px; }\n\npre {\n    margin: 0;\n    padding: 11px;\n    background: var(--input-bg);\n    colo